@font-face {
  font-family: 'Hangeuljaemin4-Regular';
  src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2307-1@1.1/Hangeuljaemin4-Regular.woff2') format('woff2');
  font-weight: normal;
  font-style: normal;
}

@media (max-width: 550px) {
  .me {
    width: 80px;
    height: 80px;
    margin-right: 20px;
  }
  .nameCard {
    width: 360px;
  }

  .nameCard p{
    margin-top: 0;
    margin-bottom: 3px;
    font-size: 12px;
  }

  .nameCard a {
    font-size: 10px;
  }

  .stack {
    width: 360px;
  }

  .stack .stackList {
    display: flex;
    width: 360px;
  }

  .stack .stackList p {
    width: 100px
  }

  div .projectHeader {
    width: 360px;
  }

  div .projectList {
    width: 360px;
  }

  div .projectList div :nth-child(2) {
    font-size: 10px;
  }

  div .projectList p {
  font-size: 12px;
  }

  div .projectList div div{
    width: 150px;
  }

  div .projectSlide {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: goldenrod;
    width: 360px;
    height: 230px;
  }
  
 div .projectSlide img {
    width: 350px;
    height: 220px;
  }

 
}
.headerLine {
  position: fixed;
  left: 0;
  top: 0;
  padding-top: 8px;
  padding-bottom: 8px;
  width: 100%;
  background-color: whitesmoke;
}

.header {
  font-weight: bold;
  font-family: Hangeuljaemin4-Regular;
  font-size: 15px;
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
  transition: background-color 0.6s ease;
}

.header:hover {
  background-color: lightsteelblue;
}

.light {
  background-color: gold;
  transition: background-color 0.8s;
  border-radius: 5px;
  padding-left: 5px;
  padding-right: 5px;
}

.projectList {
  display: flex;
  width: 500px;
}

.imageText {
  position: absolute;
  z-index: 1;
  background-color: rgba(0, 0, 0, 0.5);
  color: yellow;
  padding: 5px;
  border-radius: 5px;
}

.stackList {
  display: flex;
  width: 500px;
}

.stackList p {
  width: 200px;
}

.projectHeader {
  width: 500px;
}

.projectList div :nth-child(2) {
  width: 220px;
}

.projectSlide {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: mediumpurple;
  width: 530px;
  height: 330px;
}

.projectSlide img {
  width: 500px;
  height: 300px;
}